.SegmentedControl {
  display: -ms-flexbox;
  display: flex;
  border-radius: 8px;
  border: 1px solid #ebebeb;
  margin: 0 12px;
}
.SegmentedControl__item {
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 16px;
  line-height: 32px;
  text-align: center;
  position: relative;
  color: #1a1a1a;
  -webkit-transition: color 150ms ease-in-out;
  -o-transition: color 150ms ease-in-out;
  transition: color 150ms ease-in-out;
}
.SegmentedControl__item:after {
  content: '';
  position: absolute;
  top: -1px;
  left: -1px;
  bottom: -1px;
  right: -1px;
  border: 2px solid #5181b8;
  border-radius: 8px;
  opacity: 0;
  -webkit-transition: opacity 150ms ease-in-out;
  -o-transition: opacity 150ms ease-in-out;
  transition: opacity 150ms ease-in-out;
}
.SegmentedControl__item.selected {
  color: #5181b8;
}
.SegmentedControl__item.selected:after {
  opacity: 1;
}



