.Graph {
  padding: 16px 30px 36px 30px;
}
.Graph__cont {
  position: relative;
}
.Graph__cont svg {
  position: relative;
  z-index: 10;
}
.Graph__cont path {
  stroke: #5181b8;
}
.Graph__separator {
  position: absolute;
  width: 1px;
  height: 100%;
  top: 0;
  background: #ebebeb;
  z-index: 1;
  -webkit-transition: background 150ms ease-in-out;
  -o-transition: background 150ms ease-in-out;
  transition: background 150ms ease-in-out;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.Graph__separator.active {
  background: #5181b8;
}
.Graph__point {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #fff;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 2px solid #5181b8;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 11;
  -webkit-transition: background 150ms ease-in-out;
  -o-transition: background 150ms ease-in-out;
  transition: background 150ms ease-in-out;
}
.Graph__point.active {
  background: #5181b8;
}
.Graph__touch-zone {
  z-index: 12;
  height: 100%;
  position: absolute;
  top: 0;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 16.6%;
}
.Graph__day {
  position: absolute;
  bottom: -20px;
  left: 0;
  width: 100%;
  color: #7a7a7a;
  text-align: center;
  font-size: 12px;
  line-height: 14px;
  -webkit-transition: color 150ms ease-in-out;
  -o-transition: color 150ms ease-in-out;
  transition: color 150ms ease-in-out;
}
.Graph__touch-zone.active .Graph__day {
  color: #5181b8;
}



